<template>
<div class="main-box">
    <div  class="main-img">
        <img :src="imgSrc" :alt="imgAlt">
    </div>
</div>
</template>

<script>
import $ from 'jquery'

export default {
    name:'main-box',
    props:[
        
    ],
    data(){
        return{
            imgSrc:"./img/main-img.webp",
            imgAlt:"발로란트 요원 (제트)",
        }
    },
    created(){
        $(window).scroll(function(){
            if($(window).scrollTop()> 2158){
                $('.main-img').fadeOut(300);
            }
            if($(window).scrollTop()< 2158){
                $('.main-img').fadeIn(300);
            }
        })
    },
}
</script>

<style lang="scss" scoped>
.main-box {display: flex; justify-content: center;
    .main-img {position: fixed; width: 387px; height: 700px; top: 140px; z-index: 1;
        img{width: 387px; height: 700px;}
    }
}
</style>