<template>
  <div id="modal" v-show="modal">
    <div class="modal-content">
      <div class="modal-box">
        <button class="close-btn" @click.prevent="modalClose"><h2>×</h2></button>
        <iframe 
        width="560" 
        height="315" 
        src="https://www.youtube.com/embed/EGd2H11qKPI?si=7jPdDso8qVntOZKz" 
        title="YouTube video player" 
        frameborder="0" 
        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" 
        referrerpolicy="strict-origin-when-cross-origin" 
        allowfullscreen>
      </iframe>
      </div>
    </div>
  </div>
</template>

<script>
import EventBus from '../../EventBus.js'
export default {
  name:'ModalConponent',
  props:[],
  data(){
    return{
      modal:false,
    }
  },
  created(){
      
      EventBus.$on('modalOn',()=>{
          this.modal=true;
      })
  },
  methods:{
    modalClose(){
      this.modal=false;
    }
  }
}
</script>

<style lang="scss" scoped>
#modal {width: 100%; height: 100%; position: fixed; z-index: 4; background: rgba(0, 0, 0, 0.4);
  .modal-content {width: 100%; height: 100%;display: flex; justify-content: center; align-items: center;
    .modal-box {width: 100%; height: 100%; display: flex; justify-content: center; align-items: center;
      .close-btn {width: 75px; height: 75px; position: absolute; top: -4%; right: 5%; background: none; border: none; cursor: pointer;
        > h2 {font-size: 150px; color: #f66;}
      }
      iframe {width: 90%; height: 80%;}
    }
  }
}
</style>