<template>
  <div class="main-page-btn-box">
    <div class="btn-container">
        <span>
            <a href="#section1" class="page-btn page-btn01 blind on">page-btn1</a>
            <a href="#section2" class="page-btn page-btn02 blind">page-btn2</a>
            <a href="#section3" class="page-btn page-btn03 blind">page-btn3</a>
        </span>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'

export default {
    name:'main-page-btn-box',
    props:[],

    data(){
        return{
            
        }
    },

    created(){
        $('.page-btn').each(function(){
                $('.page-btn').on({
                    click(){
                        $('.page-btn').removeClass('on');
                        $('.page-btn').addClass('on');
                    }
                })
            });

        $(window).scroll(function(){
            if($(window).scrollTop() >= $('#section1').offset().top && $(window).scrollTop() < $('#section2').offset().top){
                $('.page-btn').removeClass('on');
                $('.page-btn').eq(0).addClass('on');
            }
            if($(window).scrollTop() >= $('#section2').offset().top && $(window).scrollTop() < $('#section3').offset().top){
                $('.page-btn').removeClass('on');
                $('.page-btn').eq(1).addClass('on');
            }
            if($(window).scrollTop() >= $('#section3').offset().top){
                $('.page-btn').removeClass('on');
                $('.page-btn').eq(2).addClass('on');
            }
            if($(window).scrollTop()> 2158){
                $('.btn-container').fadeOut(300);
            }
            if($(window).scrollTop()< 2158){
                $('.btn-container').fadeIn(300);
            }
        });
    },
}
</script>

<style lang="scss" scoped>
#main .main-page-btn-box {position: fixed; top: 50%; right: 8%; margin-top: -69px; z-index: 1;
    .btn-container {width: 100%;
    > span {display: flex; flex-flow: column;
        .page-btn {width: 16px; height: 16px; background: rgba(255, 102, 102, 0.7); margin: 15px 0; border-radius: 8px;}
        .page-btn.on {width: 19px; height: 19px; border: 3px solid #fff; border-radius: 10px;}
    }
    }
}
</style>